.site-list {
  display: flex;
  list-style: none;
  width: 100%;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    align-items: center;
    min-height: 110px;
  }
}

.site-list__item {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  width: 100%;
  color: $white;
  height: 62px;
  border-bottom: 1px solid $black;
  border-top: 1px solid $border-nav;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: bold;

  @media (min-width: $desktop-width) {
    border: none;
    width: 129px;
    font-size: 18px;
    font-weight: normal;
    line-height: 1;
    text-align: center;
    text-transform: none;

    &--active {
      position: relative;

      &::after {
        content: "";
        position: absolute;
        top: 52px;
        left: 0;
        height: 2px;
        width: 100%;
        background: $darkgrey;
      }
    }
  }
}

.site-list__item:first-of-type {
  @media (min-width: $desktop-width) {
    width: 84px;
  }
}

.site-list__item:nth-of-type(2) {
  @media (min-width: $desktop-width) {
    width: 127px;
  }
}

.site-list__item:nth-of-type(3) {
  @media (min-width: $desktop-width) {
    width: 84px;
  }
}

.site-list__item:last-of-type {
  @media (min-width: $desktop-width) {
    width: 143px;
  }
}
